import React from 'react'
import { Link } from 'react-router-dom'
import { Button, Container, Grid, Icon, Header, Segment, Modal, Statistic, Image } from 'semantic-ui-react'
import Followers from '../components/Followers'

/**
 * user profile page
 */
function ProfilePage(props) {
	return (
		<Container>
			<Header as='h3' icon textAlign='center'>
				<Icon name='user' circular />
				<Header.Content>User Profile</Header.Content>
			</Header>
			<Button
				as={Link}
				to='/edit-profile'
				content='Edit Profile'
				primary
				icon='edit'
			/>
			<Segment>
				<Grid columns={3} relaxed='very' stackable>
					<Grid.Column verticalAlign='middle'>
						{/* avatar */}
						<Image
							src={props.avatar}
							size='small'
							rounded
							centered
						/>
					</Grid.Column>
					<Grid.Column>
						<Header subheader='Username' content={props.username} />
						<Header subheader='First Name' content={props.first_name} />
						<Header subheader='Last Name' content={props.last_name} />
						<Header subheader='Email' content={props.email} />
						<Header subheader='Age' content={props.age} />
					</Grid.Column>
					<Grid.Column>
						<Statistic.Group size='tiny' horizontal>
							<Statistic className='button' label='Posts' value={props.posts} />
							<Modal
								trigger={<Statistic className='button' label='Followers' value={props.followers} />}
								header='Followers'
								content={<Followers buttons={
									<Button
										content='Remove'
										icon='user delete'
										labelPosition='left'
										color="red"
									/>
								} />}
								actions={['Close']}
							/>
							<Modal
								trigger={<Statistic className='button' label='Following' value={props.following} />}
								header='Following'
								content={<Followers buttons={
									<Button
										content='Unfollow'
										icon='user delete'
										labelPosition='left'
										color="red"
									/>
								} />}
								actions={['Close']}
							/>
						</Statistic.Group>
					</Grid.Column>
				</Grid>

			</Segment>
		</Container>
	)
}

ProfilePage.defaultProps = {
	username: 'soham.korade',
	first_name: 'Soham',
	last_name: 'Korade',
	email: 'soham@greddiit.com',
	age: '19',
	followers: '99',
	following: '100',
	posts: '42',
	avatar: 'https://react.semantic-ui.com/images/avatar/large/matthew.png'
}

export default ProfilePage