import React from "react"
// import { useEffect } from "react"
import { Outlet } from "react-router-dom"
import { get_saved_user } from "../common/auth"
import LoginPage from "../pages/LoginPage"

function ProtectedRoute({ setUser, path }) {
	const username = get_saved_user()
	// useEffect(e => setUser(username))
	return (username ? <Outlet /> : <LoginPage setUser={setUser} path={path} />)
}

export default ProtectedRoute
