import React from 'react'
import { Link } from 'react-router-dom'
import { Button, Container, Icon, Header } from 'semantic-ui-react'

/**
 * 404 Not Found page
 */
function NotFoundPage(props) {
	return (
		<Container text>
			<Header as='h2' icon textAlign='center'>
				<Icon name='warning' circular />
				<Header.Content>404 Not Found</Header.Content>
			</Header>
			<Header
				as='h2'
				content='Sorry, the page you are looking for does not exist.'
			/>
			<Button as={Link} to='/' primary size='huge'
				content='Go to Home'
				icon='home'
			/>
		</Container>
	)
}

export default NotFoundPage