import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { Button, Form, Grid, Header, Message, Segment, Input, Tab } from 'semantic-ui-react'
import { login } from '../common/auth'
import Query from '../components/Query'

/**
 * form with username and password fields
 */
function LoginForm({ error, onSubmit }) {
	return (
		<Form onSubmit={onSubmit}>
			{error && <Message
				negative
				header={error}
			/>}
			<Form.Input
				name="username"
				placeholder='Username'
				icon='user'
				iconPosition='left'
			/>
			<Form.Input
				name="password"
				placeholder='Password'
				type='password'
				icon='lock'
				iconPosition='left'
			/>
			<Button primary content='Login' type='submit' />
			<Button content='Sign Up instead' type='button' as={Link} to='/login?signup=true' />
		</Form >

	)

}

/**
 * form for registering a new user
 */
function RegistrationForm({ error, onSubmit }) {
	return (
		<Form onSubmit={onSubmit}>

			<Form.Group widths={3}>
				<Form.Field required
					control={Input}
					label='First name'
					placeholder='First name' />
				<Form.Field
					control={Input}
					label='Last name'
					placeholder='Last name'
				/>
				<Form.Field required
					control={Input}
					label='Age'
					placeholder='Age'
					type='number'
					// validation
					min={1}
					max={200}
				/>
			</Form.Group>
			<Form.Field required
				control={Input}
				label='Email'
				placeholder='Email'
				type='email' />

			<Form.Field required
				control={Input}
				label='Username'
				placeholder='Username'
				// validation
				minLength={3}
				maxLength={20}
				pattern="^[a-zA-Z0-9]+$"
				title="Must contain only letters and numbers, and between 3 and 20 characters"
			/>

			<Form.Field required
				control={Input}
				label='Password'
				placeholder='Password'
				autoComplete="new-password"
				type='password'

				// validation
				minLength={8}
				pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
				title="Must contain at least one number and one uppercase and lowercase letter, and at least 8 or more characters"
			/>
			<center>
				<Button primary content='Register' type='submit' />
				<Button content='Login instead' type='button' as={Link} to='/login' />
			</center>
		</Form >
	)
}

/**
 * login or register a new user
 */
function LoginPage({ setUser, path }) {

	const navigate = useNavigate()

	const [serverError, setServerError] = useState('')

	function onLogin(e) {
		e.preventDefault() // prevent page reload
		const formData = new FormData(e.target)

		const credentials = Object.fromEntries(formData.entries())

		const data = login(credentials)
		if (data.error) {
			setServerError(data.error)
		} else {
			setServerError('')
			setUser(credentials.username)
			navigate(path)
		}
	}

	function onSignUp(e) {
		e.preventDefault() // prevent page reload
		// const formData = new FormData(e.target)

		// const credentials = Object.fromEntries(formData.entries())

		alert('TODO: call API to register user')
	}
	return (<>
		<Header as='h1' textAlign='center'>
			{/* <Image src='../logo.svg' /> */}
			Greddiit
		</Header>
		<Segment padded='very' basic>
			<Grid columns={2} stackable centered>
				{/* <Divider vertical>Or</Divider> */}

				<Grid.Row verticalAlign='middle'>

					<Grid.Column >
						<Button.Group vertical>
							<Button content='Login with Google' type='submit' />
							<Button content='Login with Facebook' type='submit' />
						</Button.Group>
					</Grid.Column>
					<Grid.Column
					// computer={6}
					// tablet={10}
					// mobile={16}
					>

						<Tab
							menu={{ compact: true, vertical: true }}
							activeIndex={Query({ param: 'signup' }) === 'true' ? 1 : 0}
							onTabChange={(e, { activeIndex }) => navigate(activeIndex === 1 ? '/login?signup=true' : '/login')}
							panes={[
								{
									menuItem: 'Login',
									render: () => <Tab.Pane content={
										<LoginForm error={serverError} onSubmit={onLogin} />
									} />
								},
								{
									menuItem: 'Register',
									render: () => <Tab.Pane content={
										<RegistrationForm error={serverError} onSubmit={onSignUp} />
									}
									/>
								},

							]}
						/>

					</Grid.Column>


				</Grid.Row>
			</Grid>
		</Segment>
	</>
	)
}

export default LoginPage