import LoginPage from './pages/LoginPage'
import ProfilePage from './pages/ProfilePage'
import DashboardPage from './pages/DashboardPage'
import NotFoundPage from './pages/NotFoundPage'

import React, { useState } from 'react'
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom'

import { get_saved_user } from "./common/auth";

import Navbar from './components/Navbar'
import ProtectedRoute from './components/ProtectedRoute'

import 'semantic-ui-css/semantic.min.css'
import './index.css'

/**
 * App component
 */
function App() {
  const [user, setUser] = useState(get_saved_user())

  const path = window.location.pathname

  return (
    <Router>
      {user && <Navbar user={user} setUser={setUser} />}
      <Routes>
        <Route element={<ProtectedRoute setUser={setUser} path={path} />} >
          <Route exact path='/' element={<Navigate to='/profile' />} />
          <Route exact path='/dashboard' element={<DashboardPage user={user} setUser={setUser} />} />
          <Route exact path='/profile' element={<ProfilePage />} />
        </Route>
        <Route exact path='/login' element={<LoginPage setUser={setUser} path='/profile' />} />
        <Route path='*' element={<NotFoundPage />} />
      </Routes>
    </Router >
  )
}

export default App
