import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Menu } from 'semantic-ui-react';
import { logout } from '../common/auth';

/**  
 * a navbar with links to the subreddits, my subreddits, saved posts, and profile
 */
function Navbar({ user, setUser }) {
	const navigate = useNavigate()

	return (
		<Menu stackable
			items={
				[
					{
						key: 'Sub Greddiits', name: 'Sub Greddiits',
						icon: 'list'
					},
					{
						key: 'My Sub Greddiits', name: 'My Sub Greddiits',
						icon: 'list'
					},
					{
						key: 'Saved Posts', name: 'Saved Posts',
						icon: 'bookmark'
					},
					{
						key: 'Profile', name: user,
						icon: 'user',
						position: 'right',
						onClick: e => navigate('/profile')
					},
					{
						key: 'Logout', name: 'Logout',
						icon: 'log out',
						onClick: e => logout(setUser)
					},
				]
			}
		/>
	)
}

export default Navbar

